.Login-Main {
  display: flex;
  justify-content: center;

  .login-wrapper {
    width: 931px;
    height: 550px;
    position: relative;
    top: 100px;
    border-radius: 70px;
    border-style: solid;
    border-color: rgb(185, 185, 185);
    background: white;
    box-shadow: inset 17px 17px 60px #ffffff, inset -17px -17px 60px #ffffff;
    z-index: 100;
    display: flex;
    justify-content: center;

    .container {
      max-width: 350px;
      background: #f8f9fd;
      background: linear-gradient(0deg, rgb(255, 255, 255) 0%, rgb(244, 247, 251) 100%);
      border-radius: 40px;
      padding: 25px 35px;
      border: 5px solid rgb(255, 255, 255);
      box-shadow: rgba(133, 189, 215, 0.8784313725) 0px 30px 30px -20px;
      margin: 20px;
    }

    .heading {
      text-align: center;
      font-weight: 900;
      font-size: 30px;
      color: rgb(16, 137, 211);
    }

    .Login-Form-Main {
      position: relative;
      top: 35px;

      .form {
        margin-top: 20px;

        .input {
          width: 303px; //  'px'
          background: white;
          border: none;
          padding: 15px 20px;
          border-radius: 20px;
          margin-top: 15px;
          box-shadow: #cff0ff 0px 10px 10px -5px;
          border-inline: 2px solid transparent;

          &::-moz-placeholder {
            color: rgb(170, 170, 170);
          }

          &::placeholder {
            color: rgb(170, 170, 170);
          }

          &:focus {
            outline: none;
            border-inline: 2px solid #12b1d1;
          }
        }

        .forgot-password {
          display: block;
          margin-top: 10px;
          margin-left: 10px;

          a {
            font-size: 11px;
            color: #0099ff;
            text-decoration: none;
          }
        }

        .login-button {
          display: block;
          width: 100%;
          font-weight: bold;
          background: linear-gradient(45deg, rgb(16, 137, 211) 0%, rgb(18, 177, 209) 100%);
          color: white;
          padding-block: 15px;
          margin: 20px auto;
          border-radius: 20px;
          box-shadow: rgba(133, 189, 215, 0.8784313725) 0px 20px 10px -15px;
          border: none;
          transition: all 0.2s ease-in-out;

          &:hover {
            transform: scale(1.03);
            box-shadow: rgba(133, 189, 215, 0.8784313725) 0px 23px 10px -20px;
          }

          &:active {
            transform: scale(0.95);
            box-shadow: rgba(133, 189, 215, 0.8784313725) 0px 15px 10px -10px;
          }
        }
      }

      .social-account-container {
        margin-top: 25px;

        .title {
          display: block;
          text-align: center;
          font-size: 10px;
          color: rgb(170, 170, 170);
        }

        .social-accounts {
          width: 100%;
          display: flex;
          justify-content: center;
          gap: 15px;
          margin-top: 5px;

          .social-button {
            background: linear-gradient(45deg, rgb(0, 0, 0) 0%, rgb(112, 112, 112) 100%);
            border: 5px solid white;
            padding: 5px;
            border-radius: 50%;
            width: 40px;
            aspect-ratio: 1;
            display: grid;
            place-content: center;
            box-shadow: rgba(133, 189, 215, 0.8784313725) 0px 12px 10px -8px;
            transition: all 0.2s ease-in-out;

            .svg {
              fill: white;
              margin: auto;
            }

            &:hover {
              transform: scale(1.2);
            }

            &:active {
              transform: scale(0.9);
            }
          }
        }
      }

      .agreement {
        display: block;
        text-align: center;
        margin-top: 15px;

        a {
          text-decoration: none;
          color: #0099ff;
          font-size: 9px;
        }
      }
    }
  }
}
